.bio {
    background-image: -webkit-gradient(linear, left top, right top, from(#f7ca45) , to(#fada73));
    background-image: -webkit-linear-gradient(left, #f7ca45 , #fada73);
    background-image: -o-linear-gradient(left, #f7ca45 , #fada73);
    background-image: linear-gradient(to right, #f7ca45 , #fada73);
    padding-top: 30px;
    padding-bottom: 80px;
    line-height: 1rem;
}

.shadow {
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    margin-top: 25px;
    padding: 30px 50px;
    color: rgb(51, 53, 53);
}



